<template>
  <div>
    <!-- 移动端菜单切换按钮 -->
    <div class="mobile-toggle" :class="{ 'mobile-toggle-dark': isDarkTheme }" @click="toggleMobileMenu">
      <el-icon size="24">
        <Expand v-if="!isMobileMenuVisible" />
        <Fold v-else />
      </el-icon>
    </div>

    <div class="side-navigation" 
      :class="{ 
        'side-navigation-dark': isDarkTheme,
        'mobile-visible': isMobileMenuVisible
      }"
    >
      <!-- 桌面端显示LOGO -->
      <div class="side-nav-header" v-if="!isMobile">
        <div class="time-container" :class="{ 'time-container-dark': isDarkTheme }">
          <div class="date-display">{{ dateDisplay }}</div>
          <div class="time-display">{{ timeDisplay }}</div>
          <div class="week-display">{{ weekDisplay }}</div>
        </div>
      </div>

      <div class="side-nav-content">
        <div class="side-nav-search">
          <el-input
            v-model="searchText"
            placeholder="搜索分类..."
            clearable
            prefix-icon="Search"
            @input="filterCategories"
          />
          <div class="statistics-info">
            <span>当前显示: {{ filteredCategories.length }} 个分类</span>
            <span>共 {{ totalLinksCount }} 个链接</span>
          </div>
        </div>
        <el-menu
          :default-active="activeIndex"
          class="side-nav-menu"
          ref="sideMenu"
          @select="handleSelect"
        >
          <el-menu-item 
            v-for="category in filteredCategories" 
            :key="category.name" 
            :index="category.name"
            :class="{ 'is-active': activeIndex === category.name }"
          >
            <span v-if="category.emoji" class="emoji-icon">{{ category.emoji }}</span>
            <el-icon v-else>
              <component :is="category.icon || 'Folder'" />
            </el-icon>
            <span>{{ category.name }}</span>
            <span class="item-count">{{ category.links.length }}</span>
          </el-menu-item>
        </el-menu>
      </div>
      <div class="back-to-top-wrapper">
        <el-button 
          class="back-to-top-btn"
          @click="scrollToTop"
        >
          <el-icon><ArrowUpBold /></el-icon>
          返回顶部
        </el-button>
      </div>
    </div>
  </div>
</template>

<script>
import { Folder, Expand, Fold, Monitor, Search, ArrowUpBold } from '@element-plus/icons-vue'
import { navData } from "../nav-data.js"
import * as ElementPlusIcons from '@element-plus/icons-vue'
import { nextTick } from 'vue'

export default {
  name: 'SideNavigation',
  components: {
    Folder,
    Expand,
    Fold,
    Monitor,
    Search,
    ArrowUpBold,
    ...ElementPlusIcons
  },
  props: {
    isDarkTheme: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      activeIndex: '',
      categories: navData,
      isMobileMenuVisible: false,
      searchText: '',
      filteredCategories: navData,
      isMobile: window.innerWidth <= 768,
      currentTime: '',
      dateDisplay: '',
      timeDisplay: '',
      weekDisplay: '',
      timer: null
    }
  },
  computed: {
    totalLinksCount() {
      return this.categories.reduce((total, category) => total + category.links.length, 0);
    }
  },
  created() {
    this.updateTime()
    this.timer = setInterval(this.updateTime, 1000)
  },
  beforeUnmount() {
    // 清理事件监听
    if (this.handleResize) {
      window.removeEventListener('resize', this.handleResize);
    }
    // 清理定时器
    if (this.timer) {
      clearInterval(this.timer);
    }
  },
  methods: {
    filterCategories() {
      if (!this.searchText) {
        this.filteredCategories = this.categories;
        return;
      }
      const searchLower = this.searchText.toLowerCase();
      this.filteredCategories = this.categories.filter(category => 
        category.name.toLowerCase().includes(searchLower)
      );
    },
    handleSelect(index) {
      this.activeIndex = index;
      // 发送选中的分类名称
      this.$emit('category-selected', index);
      // 在移动端时，选择后自动隐藏菜单
      if (this.isMobile) {
        this.isMobileMenuVisible = false;
      }
    },
    toggleMobileMenu() {
      this.isMobileMenuVisible = !this.isMobileMenuVisible;
      // 等待 DOM 更新后再触发 resize 事件
      nextTick(() => {
        window.dispatchEvent(new Event('resize'));
      });
    },
    scrollToTop() {
      const sideMenu = this.$refs.sideMenu;
      if (sideMenu) {
        sideMenu.$el.scrollTo({
          top: 0,
          behavior: 'smooth'
        });
      }
    },
    updateTime() {
      const now = new Date()
      const year = now.getFullYear()
      const month = String(now.getMonth() + 1).padStart(2, '0')
      const day = String(now.getDate()).padStart(2, '0')
      const hours = String(now.getHours()).padStart(2, '0')
      const minutes = String(now.getMinutes()).padStart(2, '0')
      const seconds = String(now.getSeconds()).padStart(2, '0')
      const weekDays = ['星期日', '星期一', '星期二', '星期三', '星期四', '星期五', '星期六']
      const weekDay = weekDays[now.getDay()]
      
      this.dateDisplay = `${year}年${month}月${day}日`
      this.timeDisplay = `${hours}:${minutes}:${seconds}`
      this.weekDisplay = weekDay
    }
  },
  mounted() {
    // 创建一个防抖函数
    const debounce = (fn, delay) => {
      let timer = null;
      return function (...args) {
        if (timer) clearTimeout(timer);
        timer = setTimeout(() => fn.apply(this, args), delay);
      };
    };

    // 处理 resize 事件
    const handleResize = debounce(() => {
      if (window.innerWidth > 768 && !this.isMobileMenuVisible) {
        this.isMobileMenuVisible = false;
      }
      this.isMobile = window.innerWidth <= 768
    }, 100);

    window.addEventListener('resize', handleResize);

    // 保存引用以便清理
    this.handleResize = handleResize;
  },

}
</script>

